import { BpmnModel } from "./BpmnModel";
import { CatchEvent } from "./CatchEvent";

export class StartEvent extends CatchEvent {
    constructor(model: BpmnModel) {
        super(model);
    }

    readonly ClrTypeID: string = "bpmn.StartEvent";

    NewInstance(): StartEvent {
        return new StartEvent(this.Model);
    }
}